import React, {Component} from 'react'
import { connect } from 'react-redux'
import {bindActionCreators} from 'redux'
import Select2 from 'react-select2-wrapper'
import {
    get_sections,
    get_areas,
    get_zones,
    get_promoted
} from '../../actions/promovido'
import PropTypes from 'prop-types'


class Filter extends Component {
    static propTypes = {
        get_zones: PropTypes.func.isRequired,
        get_areas: PropTypes.func.isRequired,
        get_sections: PropTypes.func.isRequired,
        get_promoted: PropTypes.func.isRequired,
        Zones: PropTypes.array,
        Areas: PropTypes.array,
        Sections: PropTypes.array,
        Persons: PropTypes.array,
        Data: PropTypes.array,
        token: PropTypes.string,
        forms: PropTypes.object
    };

    constructor(props){
        super(props);
        this.onChangeZone = this.onChangeZone.bind(this);
        this.onChangeArea = this.onChangeArea.bind(this);
        this.onChangeSection = this.onChangeSection.bind(this);
    }

    componentWillMount() {
        this.props.get_zones(this.props.token);
        this.props.get_promoted(this.props.token);
    }

    onChangeZone = e => {
        e.preventDefault();
        this.props.forms.zone = e.target.value;
        this.props.get_areas(this.props.Data, this.props.forms.zone);
    };

    onChangeArea = e => {
        e.preventDefault();
        this.props.forms.area = e.target.value;
        this.props.get_sections(this.props.Data, this.props.forms);
    };

    onChangeSection(e){
        e.preventDefault();
        this.props.forms.section = e.target.value;
    }

    render() {
        const { zone, area, section } = this.props.forms;
        return (
            <div className="row">
                <div className="col-12">
                    <div className="box">
                        <div className="box-header with-border">
                            <h4 className="box-title">Área de Promoción</h4>
                            <ul className="box-controls pull-right">
                                <li><a className="box-btn-slide" href="#"/></li>
                            </ul>
                        </div>
                        <div className="box-body pb-0">
                            <div className="row">
                                <div className="col-md-4 col-12">
                                    <div className="form-group">
                                        <label>Zona</label>
                                        <Select2
                                            className="form-control select2"
                                            data={this.props.Zones}
                                            onChange={this.onChangeZone}
                                            value={zone}
                                            options={{
                                                placeholder: 'Elige una zona'
                                            }}
                                        />
                                    </div>
                                </div>
                                <div className="col-md-4 col-12">
                                    <div className="form-group">
                                        <label>Area</label>
                                        <Select2
                                            className="form-control select2"
                                            data={this.props.Areas}
                                            onChange={this.onChangeArea}
                                            value={area}
                                            options={{
                                                placeholder: 'Elige un área'
                                            }}
                                        />
                                    </div>
                                </div>
                                <div className="col-md-4 col-12">
                                    <div className="form-group">
                                        <label>Sección</label>
                                        <Select2
                                            className="form-control select2"
                                            data={this.props.Sections}
                                            onChange={this.onChangeSection}
                                            value={section}
                                            options={{
                                                placeholder: 'Elige una sección'
                                            }}
                                        />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    token: state.auth.token,
    Zones: state.promovido.Zones,
    Areas: state.promovido.Areas,
    Sections: state.promovido.Sections,
    Data: state.promovido.Data,
    forms: state.promovido.forms
});

const mapDispatchToProps = dispatch => {
    return {
        get_zones: bindActionCreators(get_zones, dispatch),
        get_areas: bindActionCreators(get_areas, dispatch),
        get_sections: bindActionCreators(get_sections, dispatch),
        get_promoted: bindActionCreators(get_promoted, dispatch)
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(Filter);