import React from 'react'
import { actionTypes } from './types'
import fetch from 'isomorphic-unfetch'

export const get_zones = token => async dispatch => {
    const response = await fetch('http://10.0.0.5:8000/api/', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            'Authorization': `Token ${token}`
        },
        body: JSON.stringify({
            app: 'territory',
            target: 'zone',
            action: 'all',
            depth: 2
        })
    });
    if (response.ok){
        return dispatch({
            type: actionTypes.PROMOTED_GET_ZONES,
            payload: await response.json()
        });
    }
};

