export const get_areas = (Data, zone) => dispatch => {
    Data.map(data => {
        if(zone == data.id) return dispatch({
            type: actionTypes.PROMOTED_GET_AREAS,
            payload: data.area
        });
    });
};

export const get_sections = (Data, form) => dispatch => {
    Data.map(data => {
        if(form.zone == data.id) data.area.map(area => {
            if(form.area == area.id) return dispatch({
                type: actionTypes.PROMOTED_GET_SECTIONS,
                payload: area.section
            })
        })
    })
};

export const get_persons = (Data, token, form, name) => async dispatch => {
    let name_section;
    Data.map(data => {
        if(form.zone == data.id) data.area.map(area => {
            if(form.area == area.id) area.section.map(section => {
                if(form.section == section.id) name_section = section.section;
            })
        })
    });
    const response = await fetch('http://10.0.0.5:8000/api/', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            'Authorization': `Token ${token}`
        },
        body: JSON.stringify({
            app: 'api',
            target: 'census',
            action: 'get',
            model: {
                search: {
                    "search": name,
                    "address__Seccion": `${name_section}`
                }
            }
        })
    });
    if (response.ok){
        return dispatch({
            type: actionTypes.PROMOTED_GET_PERSONS,
            payload: await response.json()
        });
    }
};

export const set_formPromoted = (target, value) => dispatch =>{
    return dispatch({
        type: actionTypes.PROMOTED_SET_FORM,
        target,
        value
    })
};

export const save_promoted = state => async dispatch => {
    let body;
    let formPromoted = state.promovido.formPromoted;
    if(formPromoted.userExist != null){
        body = JSON.stringify({
            app: 'api',
            target: 'promoted',
            action: 'add',
            model:{
                person: formPromoted.userExist,
                phone: formPromoted.phone,
                status: 'S',
                candidate: [state.global.active_candidate]
            }
        })
    } else {
        const response = await fetch('http://10.0.0.5:8000/api/', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${state.auth.token}`
            },
            body: JSON.stringify({
                app: 'api',
                target: 'census',
                action: 'add',
                model:{
                    last_name: formPromoted.last_name,
                    second_last_name: formPromoted.second_last_name,
                    name: formPromoted.name,
                    address:{
                        ...state.promovido.forms,
                        birthday: `${formPromoted.day}/${formPromoted.month}/${formPromoted.year}`
                    }
                }
            })
        });
        if (response.ok){
            let person = await response.json();
            body = JSON.stringify({
                app: 'api',
                target: 'promoted',
                action: 'add',
                model:{
                    person: person.id,
                    phone: formPromoted.phone,
                    status: 'S',
                    candidate: [state.global.active_candidate]
                }
            })
        } else {
            return {
                error: 'error'
            }
        }

    }
    const response = await fetch('http://10.0.0.5:8000/api/', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            'Authorization': `Token ${state.auth.token}`
        },
        body
    });
    if (response.ok){
        return dispatch({type: actionTypes.SAVE_PROMOTED});
    }
};